.my-who{
    overflow: hidden;
    color:#FFAED8;
    position: relative;
    
    &__content{
        background: #1F1A1C;
        padding: 0;
        overflow: hidden;
    }
    &__name{
        //font-size: 10vw;
        //font-weight: 500;
        //margin: 0px;
    }

    &__animation {
        margin: 0 auto;
        white-space: nowrap;
        overflow: hidden;
        position: absolute;
        top: 50px;
    }

    &__animation{
        p
        {
            display: inline-block;
            padding-left: 100%;
            animation: marquee 14s linear infinite;
        }
    }

    &__animation--2{
        p {
            animation-delay: 7s
        }
    }

    &__info{
        
        //padding-top: clamp(150px, 16vw , 300px);
        //width: 50vw;
        font-size: 2.1vw;
        font-weight: 500;
        //margin-left: 9vw;
        p{
            //transition: transform 0.75s ease-out;
            //transform: translateX(-300px);
            //font-size: clamp(20px, 2.5vw, 40px);
            //font-family: "Righteous", cursive;
            letter-spacing: 0.05rem;
        }
    }     
}

@media screen and (max-width:1200px){
    .my-who{
        &__text{
            transform: translateX(-250px);
        }
    }
}

@media screen and (max-width:800px){
    .my-who{
        &__text{
            //padding-top: 200px;
            transform: translateX(0px);
        }
    }
}
.carrousel {
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    position: relative;
    p:nth-of-type(1){
        animation-delay: 0s;
    }
    p:nth-of-type(2){
        animation-delay: 5s;
    }
    &--hidden{
        .frase:nth-of-type(1){
            visibility: hidden;
        }
        .frase:nth-of-type(2){
            visibility: hidden;
        }  
    }
  }
.frase {
    //padding: 0 35px; /* Espaciado para el texto */
    white-space: nowrap;
    position: absolute;
    top: 50%; /* Centra verticalmente */
    
    transform: translate(0, -50%); 
    opacity: 0;
    animation: scroll 10s linear infinite;
}



  @keyframes scroll {
    0%, 100% { opacity: 1; }
    0% { transform: translate(100%, -50%); }
    100% { transform: translate(-100%, -50%); }
  }

